import React from 'react';
import Navigation from './Navigation';

import './BgGenerator.css';


class BgGenerator extends React.Component {
	constructor() {
		super();
		this.state = {
			color1: '#ff0000',
			color2: '#ffff00'
		}
	}

	componentWillUnmount() {
		// document.body.classList.remove('BgGenerator-background')
		document.body.style.background = ('')
	}

	
	onColor1Change = (event) => {
		this.setState({color1: event.target.value})
	}

	onColor2Change = (event) => {
		this.setState({color2: event.target.value})
	}


	render() {
		document.body.style.background = ('linear-gradient(to right,'
											+ this.state.color1 + ','
											+ this.state.color2 + ')')
		
		return(
			<div>
				<Navigation onMainRouteChange={this.props.onMainRouteChange}/>
				<title>Gradient Background</title>
				<h1 className='BgGenerator-h1'>Background Generator</h1>
					<div className='tc'>
						<input 
							className="mr2 color1" 
							type="color" 
							value={this.state.color1}
							onChange={this.onColor1Change} />
						<input 
							className="ml2 color2" 
							type="color" 
							value={this.state.color2} 
							onChange={this.onColor2Change} />
					</div>
				<h2 className='BgGenerator-h2'>Current CSS Background</h2>
				<h3 className='BgGenerator-h3'>{document.body.style.background.toString()};</h3>
				<h3 className='BgGenerator-h3'>{`liner-gradient(to right, ${this.state.color1}, ${this.state.color2} )`};</h3>
			</div>
		);
	}
}

export default BgGenerator;