import React from 'react';

import roboFriendsImage from '../../RoboFriends/roboFriendsImage.png';
import faceRecognitionImage from '../../FaceRecognition/faceRecognitionImage.png';
import bgGeneratorImage from '../../BgGenerator/bgGeneratorImage.png';

import './Project.css';

let projectImage;


class Project extends React.Component {
	routeFunctionImage = () => {
		switch(this.props.projectImage) {
			case 'RoboFriendsImage':
				projectImage = roboFriendsImage;
				break;
			case 'FaceRecognitionImage':
				projectImage = faceRecognitionImage;
				break;
			case 'BgGeneratorImage':
				projectImage = bgGeneratorImage;
				break; 
			default:
				break;
		}	
	}

	routeFunctionMainRoute = () => {
		let mainRoute = 'Main';
		switch(this.props.projectImage) {
			case 'RoboFriendsImage': return mainRoute = 'Robo';
			case 'FaceRecognitionImage': return mainRoute = 'Face';
			case 'BgGeneratorImage': return mainRoute = 'Bgen';
			default: 
				console.log(mainRoute); 
				return mainRoute;
		}	
	}
	

	render() {
		return (
			<div className='ImageSize center ma2 pointer grow shadow-1 br4'>
				{this.routeFunctionImage()}
								
				<p className='ml3 underline fw6 f5 light-silver'>{this.props.projectName}</p>
				<img 
					className='br4' 
					alt='' 
					src={projectImage} 
					onClick={() => this.props.onMainRouteChange(this.routeFunctionMainRoute())}
				/>	

			</div>
		);

	}
}

export default Project;