import React from 'react';
import { Scrollbars } from 'react-custom-scrollbars';

import roboProjectDetails from '../../RoboFriends/roboProjectDetails';
import faceRecognitionProjectDetails from '../../FaceRecognition/faceRecognitionProjectDetails';
import bgGeneratorProjectDetails from '../../BgGenerator/bgGeneratorProjectDetails';

import './Project.css';

const Project = (props) => {
	let projectDetails;
	switch(props.projectImage) {
		case 'RoboFriendsImage':
			projectDetails = roboProjectDetails;
			break;
		case 'FaceRecognitionImage':
			projectDetails = faceRecognitionProjectDetails;
			break;
		case 'BgGeneratorImage':
			projectDetails = bgGeneratorProjectDetails;
			break; 
		default:
			break;
	}	

	return (

		<div className='details ma2 br4'>
			<Scrollbars style={{}}>
			
				<div className='ml3 mr3 f6'>
					<p className='mb2 underline'>Description:</p>
					<p className='pa0 ma0 '><em>{projectDetails.description}</em></p>
				</div>

				<br />

				<div className='ml3 mr3 f6'>
					<p className='mb2 underline'>Technologies:</p>
					<div className='flex mt0 mr0 mb1 ml0 pa0'>
						<p className='pl2 ma0 w-50'>Front-End:</p>
						<p className='ma0'><em>{projectDetails.technology.frontend}</em></p>
					</div>
					<div className='flex ma0 pa0'>
						<p className='pl2 mt1 w-50'>Back-End:</p>
						<p className='mt1'><em>{projectDetails.technology.backend}</em></p>
					</div>
				</div>
			</Scrollbars>
		</div>

	);
}

export default Project;