import React from 'react';

const Navigation = ({onMainRouteChange}) => {
	return (
		<div className='w-100 tr' style={{position: 'fixed', fontFamily: 'courier'}}>
			<p 
				className='mt0 mr4 dib lightest-blue f3 underline pointer dim'
				onClick={() => onMainRouteChange('Main')}>Home</p>
		</div>
	);
}

export default Navigation;