import React from 'react';
import ProjectNameAndImage from './ProjectNameAndImage';
import ProjectDetails from './ProjectDetails';


class Project extends React.Component {
	render() {
		return(
			<div className='Project ma2 br4 shadow-1 '>
				<ProjectNameAndImage 
					projectName={this.props.projectName} 
					projectImage={this.props.projectImage}
					onMainRouteChange={this.props.onMainRouteChange} 
					/>
				<ProjectDetails projectImage={this.props.projectImage} />
			</div>
		);
	}
}


export default Project;