import React from 'react';
import pierreImage from './pierre.jpg';


const ProfileImage = () => {
	return(
		<div style={{position: 'absolute', left: '78%'}} 
		   	 className='w-10'>
		   	<img className='br4 shadow-1' alt='' src={pierreImage} />
		</div>			
	);
}

export default ProfileImage;